<template>
	<view class="">
		<view class="title" v-if="iconClassList.length!=0"><text>人力资源管理</text></view>
		<uni-grid :column="4" :show-border="false" :square="false">
			<uni-grid-item  style="padding-bottom: 0;" v-for="(item,index) in iconClassList" :hor="0" :ver="0" marker="badge" type="error" text="12" :key="index">
				<view :style="{background:item.color}" class="rediu" @click="gotoAdd(index,'0')">
					<!-- <uni-icons :type="item.name" color="#ffffff" size="30" /> -->
					<i :class="item.name" style="color: #FFFFFF;"></i>
				</view>
				<text class="text">{{item.text}}</text>
			</uni-grid-item>
		</uni-grid>
		<view class="title" v-if="financial.length!=0"><text>财务管理</text></view>
		<uni-grid :column="4" :show-border="false" :square="false">
			<uni-grid-item  style="padding-bottom: 0;" v-for="(item,index) in financial" :hor="0" :ver="0" marker="badge" type="error" text="12" :key="index">
				<view :style="{background:item.color}" class="rediu" @click="gotoAdd(index,'1')">
					<i :class="item.name" style="color: #FFFFFF;"></i>
				</view>
				<text class="text">{{item.text}}</text>
			</uni-grid-item>
		</uni-grid>
		<view class="title" v-if="administration.length!=0"><text>行政管理</text></view>
		<uni-grid :column="4" :show-border="false" :square="false">
			<uni-grid-item  style="padding-bottom: 0;" v-for="(item,index) in administration" :hor="0" :ver="0" marker="badge" type="error" text="12" :key="index">
				<view :style="{background:item.color}" class="rediu" @click="gotoAdd(index,'2')">
					<i :class="item.name" style="color: #FFFFFF;"></i>
				</view>
				<text class="text">{{item.text}}</text>
			</uni-grid-item>
		</uni-grid>
		<view v-if="administration.length==0&&financial.length==0&&iconClassList.length==0" class="work-no-peim">
			<i class="iconfont icon-tupianjiazaishibai02"></i>
			<view>您还没有任何操作权限哦！</view>
			<view>请联系管理员为您配置相对应的权限~</view>
		</view>
	</view>
</template>

<script>
	import uniGrid from '@/components/uni-grid/uni-grid.vue'
	import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
	import uniIcons from '@/components/uni-icons/uni-icons.vue';
	export default {
		components: {
			uniGrid,
			uniIcons,
			uniGridItem
		},
		data() {
			return { 
				user:{},
				//人力资源
				iconClassList:[],
				//财务管理
				financial:[],
				//行政
				administration:[],
				activeIndex: -1,
				checked: false
			}
		},
		onLoad() {
			let this_=this
			this.urlRequestPost('/phoneIndexData','',null,function(res){
				console.log(res.data.menus)
				let menuId1=0//人力资源menuId
				let menuId2=0//财务menuId
				let menuId3=0//行政menuId
				for (let i in res.data.menus) {
					if(res.data.menus[i].remark=='iconClassList'&&res.data.menus[i].menuType=='M'){
						menuId1=res.data.menus[i].menuId
					}else if(res.data.menus[i].remark=='administration'){
						menuId2=res.data.menus[i].menuId
					}else if(res.data.menus[i].remark=='financial'){
						menuId3=res.data.menus[i].menuId
					}
				}
				for (let i in res.data.menus) {
				   if(res.data.menus[i].parentId==menuId1&&menuId1!=0){//人力资源
					let data={
						name: 'iconfont '+res.data.menus[i].perms,
						text:res.data.menus[i].menuName,
						url:'..'+res.data.menus[i].url,
						color:res.data.menus[i].remark
					}
					this_.iconClassList.push(data)
				   }else if(res.data.menus[i].parentId==menuId2&&menuId2!=0){//行政
					let data={
						name: 'iconfont '+res.data.menus[i].perms,
						text:res.data.menus[i].menuName,
						url:'..'+res.data.menus[i].url,
						color:res.data.menus[i].remark
					}
					this_.administration.push(data)
				   }else if(res.data.menus[i].parentId==menuId3&&menuId3!=0){//财务
					let data={
						name: 'iconfont '+res.data.menus[i].perms,
						text:res.data.menus[i].menuName,
						url:'..'+res.data.menus[i].url,
						color:res.data.menus[i].remark
					}
					this_.financial.push(data)
				   }
				}
			})
		},
		methods: {
			gotoAdd(index,flag) {
				this.activeIndex = index
				if(flag=='0'){//人力资源管理
					uni.navigateTo({
					    url: this.iconClassList[this.activeIndex].url+"?edit=0",
					});
				}else if(flag=='1'){//财务管理
					uni.navigateTo({
					    url: this.financial[this.activeIndex].url+"?edit=0",
					});
				}else if(flag=='2'){//行政管理
					uni.navigateTo({
					    url: this.administration[this.activeIndex].url+"?edit=0",
					});
				}
				
			}
		}
	}
</script>

<style>
	@import url("../../common/myFont.css");
	.work-no-peim{
		text-align: center;
		padding: 50upx 0;
		color: #BCBCBC;
	}
	.work-no-peim .iconfont{
		font-size: 80upx;
		position: relative;
		left: 0;
		color: #BCBCBC;
	}
	.text {
		padding: 0 20upx;
		text-align: center;
		color: #777;
		font-size: 24upx;
		
	}
	.rediu{
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		text-align: center;
		position: relative;
	}
	.color{
		background: #1AAD19;
	}
	.iconfont {
		position: absolute;
		font-size: 50upx;
		left: 50%;
		top: 5upx;
		margin-left: -25upx;
	}
	/* title */
	.title{
		font-size: 28upx;
		text-align: left;
		padding-left: 32upx;
		padding-bottom: 20upx;
		padding-top: 40upx;
		position: relative;
	}
	.title text{
		color: #555555;
		font-weight: bold;
		padding-left: 20upx;
	}
	.title text::after{
		content: ' ';
		position: absolute;
		width: 8upx;
		height: 32upx;
		background-color: #4CA2FF;
		left: 32upx;
		top: 50%;
		margin-top: -5upx;
		
	}
</style>
